import React, {Component} from 'react';
import {Form, Row, Col, Button} from 'antd';
import {CommonInput, CommonSelect} from 'Form';
import '../style/operateList-form.css';
import {API_GET_MONITOR_CHANNEL_SOURCE} from 'api';
import {formItemLayout} from 'enum';
import style from '../style/operateList-form.scss';

const syncStatus = [
  {value: '操作类型', code: ''},
  {value: '新增行', code: 'NEW_ITEM'},
  {value: '删除行', code: 'DEL_ITEM'},
  {value: '确认发货', code: 'CONFIRM_OUTSTOCK'},
  {value: '修改订单', code: 'EDIT_ORDER'},
  // {value: '导入发货', code: 'IMPORT_OUTSTOCK'},
  {value: '分拣称重', code: 'PICKING'},
  {value: '打印出库单', code: 'OUTSTOCK'}
]

class App extends Component {
  constructor() {
    super()
    this.state = {
      channelConfig: {
        api: API_GET_MONITOR_CHANNEL_SOURCE
      }
    }
  }
  handleSearch = () => {
    this.props.form.validateFields((err, values) => {
      if (err) return
      const data = {
        orderNo: values.orderNo || '',
        operateType: values.operateType,
        keyword: values.keyword || '',
        orderDateStart:
          values.DateArray && values.DateArray.length > 0
            ? values.DateArray[0].format('YYYY-MM-DD HH:mm:ss')
            : '',
        orderDateEnd:
          values.DateArray && values.DateArray.length > 0
            ? values.DateArray[1].format('YYYY-MM-DD HH:mm:ss')
            : ''
      }
      this.props.searchData(data)
    })
  };
  render() {
    const {
      form: {getFieldDecorator, setFieldsValue, getFieldValue}
    } = this.props
    const {channelConfig} = this.state
    return (
      <Row className={style.form}>
        <Col span={7}>
          <CommonInput
            getFieldDecorator={getFieldDecorator}
            label="订单号"
            decorator="orderNo"
            placeholder="请输入"
            {...formItemLayout}
          />
        </Col>
        <Col span={7}>
          <CommonInput
            getFieldDecorator={getFieldDecorator}
            label="关键词"
            decorator="keyword"
            placeholder="请输入商品编码/名称"
            {...formItemLayout}
          />
        </Col>
        <Col span={7}>
          <CommonSelect
            getFieldDecorator={getFieldDecorator}
            label="操作类型"
            showSearch
            decorator="operateType"
            list={syncStatus}
            placeholder="操作类型"
            initialValue={''}
            {...formItemLayout}
          />
        </Col>
        <Col span={3}>
          <Row type="flex" justify="end" className={style.mb15}>
            <Button
              htmlType="submit"
              type="primary"
              onClick={this.handleSearch}
              className={style.searchButton}
            >
              查询
            </Button>
          </Row>
        </Col>
      </Row>
    )
  }
}

export default Form.create()(App)



