import React, {Component} from 'react'
import {message} from 'antd'
import {Bread} from 'Bread'
import {Form, Table} from './components'
// import {getPermissionsButtonsBans} from 'util'
import {fetchMonitorOrdersList, fetchMonitorSyncManual} from './components/actions'
import style from './style/operateList-index.scss'

// console.log(!permissionsButtonsBans)

// const !permissionsButtonsBans = getPermissionsButtonsBans()
// 手工同步
// function hasManualSyncAuth() {
//   return !permissionsButtonsBans.includes('MONITORLIST-SYNC') // 固定字段 菜单权限配置
// }

class App extends Component {
  constructor() {
    super()
    this.state = {
      tableLoading: false, //表格 loading
      tableData: [],
      selectionIds: [],
      pageIndex: 1,
      totalNum: 0,
      pageSize: 50,
      searchData: {},
      selectedRowKeys: []
    }
  }

  componentWillMount() {
    this._fetchOperateData({})
  }

  _fetchOperateData = (data, pageIndex = 1, pageSize = 50) => {
    const searchParams = {
      ...data,
      size: pageSize,
      page: pageIndex
    }
    this.setState({
      tableLoading: true
    })
    fetchMonitorOrdersList({searchParams}).then(json => {
      this.setState({
        tableLoading: false
      })
      if (json.message == 'success') {
        this.setState({
          tableData: json.page.result || [],
          pageIndex,
          totalNum: json.page.totalNum,
          pageSize,
          searchData: data
        })

        this._initSelectedRows()
      }
    })
  }
  _fetchSearchDate = (data) => {
    const {pageSize} = this.state
    this._fetchOperateData(data, 1, pageSize)
  }

  _initSelectedRows = () => {
    this.setState({
      selectionIds: []
    })
  }

  _onPageChange = (page) => {
    const {pageSize, searchData} = this.state
    this._fetchOperateData(searchData, page, pageSize)
  }
  _handleShowSizeChange = (current, pageSize) => {
    const {searchData} = this.state
    this._fetchOperateData(searchData, 1, pageSize)
  }
  _rowSelectionOnChange = ids => {
    this.setState({
      selectionIds: ids
    })
  }
  manualSync = () => { //手动更新后获取最新的列表数据渲染页面
    const ids = this.state.selectionIds
    fetchMonitorSyncManual({searchParams: {ids: ids.join(',')}}).then(json => {
      if (json.code === 200000) {
        message.success(json.message)
        const {pageSize, searchData, pageIndex} = this.state
        this._fetchOperateData(searchData, pageIndex, pageSize)
      }
    })
  }
  render() {
    const {tableData, totalNum, pageIndex, pageSize, selectionIds, tableLoading} = this.state
    return (
      <section>
        <Bread route={['监控模块', '操作日志']} />
        <div className={style.monitorList}>
          <Form
            searchData={this._fetchSearchDate}
          />
          <div className={style.listTitle}>
            <div style={{fontSize: 15, fontWeight: 'bold'}}>日志列表</div>
          </div>
          <Table
            loading={tableLoading}
            dataSource={tableData}
            totalNum={totalNum}
            pageIndex={pageIndex}
            onPageChange={this._onPageChange}
            handleShowSizeChange={this._handleShowSizeChange}
            rowSelectionOnChange={this._rowSelectionOnChange}
            size={pageSize}
            selectedRowKeys={selectionIds}
          />
        </div>
      </section>
    )
  }
}

export default App



