import {getData} from 'util'
import {
  API_GET_OPERATE_LOGS_CHANGE_LOG,
} from 'api'

/* 查询操作日志 */
export function fetchMonitorOrdersList(params) {
  return getData(Object.assign({
    api: API_GET_OPERATE_LOGS_CHANGE_LOG
  }, params))
}



