import React, {Component} from 'react'
import {Table} from 'antd'
import moment from 'moment'
// render:val=>(<div>{moment(val,'YYYY-MM-DD HH:mm:ss')}</div>)
const columns = [
  {title: '', dataIndex: '', key: '', width: 10},
  {title: '订单号', dataIndex: 'orderNo', key: 'orderNo', width: 100},
  {
    title: '操作时间',
    dataIndex: 'changeTime',
    key: 'changeTime',
    width: 100,
    render: val => <span>{moment(val).format('YYYY-MM-DD HH:mm:ss')}</span>
  },
  {title: '操作人', dataIndex: 'operatorName', key: 'operatorName', width: 100},
  {
    title: '操作类型',
    dataIndex: 'operateName',
    key: 'operateName',
    width: 100
  },
  {
    title: '操作内容',
    dataIndex: 'changeContent',
    key: 'changeContent',
    width: 200
  }
]

export default class App extends Component {
  render() {
    const {
      dataSource,
      totalNum,
      onPageChange,
      pageIndex,
      handleShowSizeChange,
      size,
      // selectedRowKeys,
      loading
    } = this.props
    // const rowSelection = {
    //   onChange: selectedRowKeys => {
    //     this.props.rowSelectionOnChange(selectedRowKeys)
    //   },
    //   selectedRowKeys
    // }
    return (
      <Table
        loading={loading}
        style={{background: '#ffffff'}}
        columns={columns}
        dataSource={dataSource.map((o, i) => ({...o, key: o.id}))}
        pagination={{
          total: totalNum,
          current: pageIndex,
          onChange: onPageChange,
          showQuickJumper: true,
          showTotal: (total, range) => `共 ${total} 条`, // 显示总数
          style: {textAlign: 'center', float: 'none'},
          pageSizeOptions: ['50', '100', '200', '500'],
          pageSize: size,
          showSizeChanger: true, // 是否可以改变 pageSize
          onShowSizeChange: handleShowSizeChange // pageSize 变化的回调
        }}
      />
    )
  }
}



